export default {
	data() {
		return {
			show:false
		}
	},
	methods: {
		randomColor(){
			var random1=Math.floor(Math.random()*225);
			var random2=Math.floor(Math.random()*225);
			var random3=Math.floor(Math.random()*225);
			return `rgb(${random1},${random2},${random3})`
		},
		scrolltolower(){
			console.log('滚动到底了')
			this.show=true;
			setTimeout(()=>{
				this.show=false
			},3000)
		}
	}
}
